@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  background: linear-gradient(45deg, lightblue, pink);
}

label {
  color: #6d6a6a;
}

input {
  padding: 5px;
}

input:focus:invalid {
  box-shadow: 0 0 5px 3px red;
}

input:focus:valid {
  box-shadow: 0 0 5px 3px green;
}

.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.flex-col-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.modal {
  padding: 50px 20px;
  margin: 10vh auto 0 auto;
  border-radius: 5px;
  background: white;
  box-shadow: 0 0 5px 1px gray;
  font-family: Roboto, 'sans-serif';
  text-align: center;
}

.home {
  min-height: 400px;
  height: 60vh;
  min-width: 300px;
  width: 25vw;
}

#welcome {
  padding: 5px;
  margin: 5px;
}

#product {
  width: 200px;
  padding: 10px;
}

#submit {
  padding: 10px 30px;
  border: 0;
  border-radius: 10px;
  margin: 20px auto 0 auto;
  color: white;
  font-weight: bold;
  font-size: 15px;
  background: #f5af19;
  transition: background 0.5s;
}

#submit:hover {
  cursor: pointer;
}

#submit:active {
  background: grey; 
}

.search {
  /*background: grey;*/
  width: 500px;
  margin: 10vh auto;
  font-family: Roboto;
}

.result {
  min-height: 200px;
  height: 40vh;
  min-width: 470px;
  width: 25vw;
}

.eff-color-box {
  background: linear-gradient(90deg, red, green);
  width: 100px;
  height: 20px;
  border-radius: 30px;
}

.product-effScore {
  width: 300px;
  height: 200px;
}

.product-price {
  color: brown;
}

.loading {
  font-family: Roboto;
  font-size: 30px;
  margin-top: 10vh;
}
